body {
    margin: 0;
}

.link {
    @extend %link;
    display: block;
    cursor: pointer;
}

.list {
    @extend %list;
}

.masters {
    background: $bgLightColor;
}

.master-list {
    @media screen and (min-width: $desktop) {
        display: flex;
    }
}

.master-item {
    @media screen and (max-width: 1279px) {
        margin-bottom: 64px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    @media screen and (min-width: $desktop) {
        margin-right: 30px;
        width: 370px;
        &:last-child {
            margin-right: 0;
        }
    }
}

.master-img {
    display: block;
    width: 100%;
    object-fit: cover;
    @media screen and (max-width: 767px) {
        width: 418px;
        margin-right: auto;
        margin-left: auto;
    }
    @media screen and (min-width: $tablet) {
        width: 450px;
        margin-right: auto;
        margin-left: auto;
    }
    @media screen and (min-width: $desktop) {
        width: 370px;
        height: 348px;
    }
}

.description-master {
    position: relative;
    margin: 0;
    margin-left: 80px;
    font-family: $textFont;
    font-style: normal;
    font-weight: 600;
    font-size: 11px;
    line-height: 1.36;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    @media screen and (min-width: $tablet), (max-width: 1279px) {
        padding-left: 120px;
    }
    @media screen and (max-width: 767px) {
        margin-left: 80px;
        padding-left: 0;
    }
    @media screen and (min-width: $desktop) {
        padding-left: 0;
    }
    color: $ordinaryColor;
    &::before {
        content: '';
        position: absolute;
        display: block;
        top: 7px;
        left: -80px;
        background-color: $ordinaryColor;
        width: 60px;
        height: 1px;
        @media screen and (min-width: $tablet), (max-width: 1279px) {
            left: 43px;
        }
        @media screen and (max-width: 767px) {
            left: -80px;
        }
        @media screen and (min-width: $desktop) {
            left: -80px;
        }
    }
}

.section-title {
    margin: 0;
    margin-top: 20px;
    margin-bottom: 44px;
    font-family: $headerFont;
    font-style: normal;
    font-weight: 700;
    font-size: 42px;
    line-height: 1.16;
    letter-spacing: 0.05em;
    color: $ordinaryColor;
    @media screen and (min-width: $tablet), (max-width: 1279px) {
        padding-left: 120px;
    }
    @media screen and (max-width: 767px) {
        padding-left: 0;
    }
    @media screen and (min-width: $desktop) {
        padding-left: 0;
    }
}

.master-title {
    margin: 0;
    margin-top: 30px;
    margin-bottom: 8px;
    font-family: $textFont;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.38;
    text-align: center;
    letter-spacing: 0.02em;
    color: $ordinaryColor;
}

.master-subtitle {
    margin: 0;
    font-family: $textFont;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.35;
    text-align: center;
    letter-spacing: 0.02em;
    color: #ff6c00;
}

.social-list {
    display: flex;
    justify-content: center;
    margin-top: 30px;
}

.social-item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 44px;
    height: 44px;
    border-radius: 50%;
    cursor: pointer;
    background: $contrastColor;
    margin-right: 10px;
    &:last-child {
        margin-right: 0;
    }
}

.icon-socials {
    transition: fill $transition;
    .social-item:hover &,
    .social-item:focus & {
        fill: #ff6c00;
    }
}

.social-item>.link {
    display: flex;
}