.container-about {
    @media screen and (min-width: $desktop) {
        margin: 0 auto;
    }
}

.gallery {
    @extend %list;
    @media screen and (max-width: 767px) {
        display: none;
    }
    @media screen and (min-width: $tablet) {
        margin-right: 30px;
    }
    @media screen and (min-width: $desktop) {
        display: flex;
        align-items: center;
        margin-right: 64px;
    }
    .item {
        @media screen and (min-width: $tablet) {
            width: 330px;
        }
        @media screen and (min-width: $desktop) {
            width: 270px;
        }
    }
    .item:not(:last-child) {
        @media screen and (min-width: $tablet) and (max-width: 1279px) {
            margin-bottom: 30px;
        }
        @media screen and (min-width: $desktop) {
            margin-right: 30px;
        }
    }
}

.flex-about {
    display: flex;
    align-items: center;
}

.name-section {
    margin-bottom: 20px;
    margin-left: 80px;
    position: relative;
    font-weight: 600;
    font-size: 11px;
    line-height: 1.36;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: $ordinaryColor;
    &::before {
        content: '';
        position: absolute;
        display: block;
        top: 7px;
        left: -80px;
        background-color: $ordinaryColor;
        width: 60px;
        height: 1px;
    }
}

.title-about {
    margin-bottom: 20px;
    font-family: $headerFont;
    font-weight: bold;
    font-size: 28px;
    line-height: 1.2;
    letter-spacing: 0.05em;
    color: $ordinaryColor;
    @media screen and (min-width: $desktop) {
        font-size: 42px;
        line-height: 1.17;
    }
}

.description {
    margin-bottom: 20px;
    font-size: 16px;
    line-height: 1.62;
    @media screen and (min-width: $desktop) {
        font-size: 18px;
        line-height: 1.67;
    }
}

.main-text {
    margin-bottom: 40px;
    font-size: 14px;
    line-height: 1.86;
}

.button-about {
    @extend %link;
    display: inline-block;
    padding: 12px 20px;
    border: 1px solid $accentColor;
    border-radius: 25px;
    font-weight: 600;
    font-size: 12px;
    line-height: 1.3;
    letter-spacing: 0.09em;
    text-transform: uppercase;
    cursor: pointer;
    transition: color $transition, background-color $transition;
    &:hover,
    &:focus,
    &:active {
        color: $contrastColor;
        background-color: $accentColor;
    }
}