.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(90, 90, 90, 0.9);
    transition: opacity $transition;
    overflow: hidden;
    &.is-hidden {
        opacity: 0;
        pointer-events: none;
    }
}

body.modal-open {
    overflow: hidden;
}

.modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
}

.map-close {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
    top: 8px;
    right: 8px;
    width: 30px;
    height: 30px;
    margin: 0;
    padding: 0;
    border: 1px solid $textColor;
    border-radius: 50%;
    cursor: pointer;
    &:hover .icon-close,
    &:focus .icon-close {
        fill: $accentColor;
    }
    &:hover,
    &:focus {
        border-color: $accentColor;
    }
}

.modal {
    width: 70%;
    height: 70%;
    border-radius: 4px;
    overflow: hidden;
}

.map-container {
    width: 100%;
    height: 100%;
}

.map-frame {
    width: 100%;
    height: 100%;
}